@charset "UTF-8";
html,
body {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
}
#app {
  height: 100%;
}
.el-message {
  top: 60px !important;
}
.el-tree-node__expand-icon {
  font-size: 20px !important;
}
/*********** */
::-webkit-scrollbar {
  width: 5px;
  height: 4px;
  background-color: transparent;
}
/* +*/
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background: transparent;
}
/* +*/
::-webkit-scrollbar-thumb {
  border-radius: 3px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: rgba(255, 255, 255, 0.8);
}
::-webkit-scrollbar-thumb:hover {
  border-radius: 3px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: rgba(255, 255, 255, 0.8);
}
.base-timeline {
  list-style: none;
  margin: 0;
  padding: 0;
}
.base-timeline-item {
    margin: 0 !important;
    padding: 0 0 10px 0;
    list-style: none;
    position: relative;
}
.base-timeline-item-tail {
      height: 100%;
      border-left: 1px solid #2D8cF0;
      position: absolute;
      left: 7px;
      top: 0;
}
.base-timeline-item-pending .base-timeline-item-tail {
      display: none;
}
.base-timeline-item-head {
      width: 13px;
      height: 13px;
      background-color: #fff;
      border-radius: 50%;
      border: 1px solid transparent;
      position: absolute;
}
.base-timeline-item-head-blue {
        border-color: #2d8cf0;
        color: #2d8cf0;
}
.base-timeline-item-head-red {
        border-color: #ed3f14;
        color: #ed3f14;
}
.base-timeline-item-head-green {
        border-color: #19be6b;
        color: #19be6b;
}
.base-timeline-item-head-custom {
      width: 40px;
      height: auto;
      margin-top: 6px;
      padding: 3px 0;
      text-align: center;
      line-height: 1;
      border: 0;
      border-radius: 0;
      font-size: 14px;
      position: absolute;
      left: -13px;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%);
}
.base-timeline-item-content {
      padding: 1px 1px 5px 24px;
      font-size: 14px;
      position: relative;
      top: -3px;
}
.base-timeline-item:last-child .base-timeline-item-tail {
      display: none;
}
.base-timeline-pending .base-timeline-item:nth-last-of-type(2) .base-timeline-item-tail {
    border-left: 1px solid #2D8cF0;
}

.el-header {
  padding: 0px !important;
}
.main-sidear {
  min-height: calc(100vh - 50px);
}
.main {
  position: relative;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
}
.main .main-notic {
    position: absolute;
    top: 0px;
    left: 0px;
}
.showpad {
  padding: 5px 20px !important;
}
.hidpad {
  padding: 0px !important;
}
.sidebar {
  display: block;
  background-color: #545c64;
}

.base-header {
  height: 50px;
  position: relative;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.base-header .header-menu {
    height: 50px;
    font-size: 30px;
    color: #ccc;
    padding: 0 10px;
    line-height: 50px;
    overflow: hidden;
}
.base-header .header-menu:hover {
      cursor: pointer;
      color: #fff;
}
.base-header .header-menu .is-active {
      background: rgba(44, 44, 46, 0.31) !important;
}
.base-header .header-logo {
    float: left;
    width: 250px;
    height: 50px;
}
.base-header .header-menu-cll {
    height: 50px;
    float: left;
    line-height: 50px;
    padding: 0 10px;
    color: #ccc;
    font-size: 30px;
}
.base-header .header-menu-cll:hover {
      cursor: pointer;
      color: #fff;
}
.base-header .header-menu-cll .header-menu-default {
      margin-left: 5px;
      -webkit-transition: all 0.3s;
      transition: all 0.3s;
}
